.cntPerson {
  display: flex;
  flex-flow: column;
  align-items: center;
  text-align: center; }

.personName {
  font-weight: bold;
  font-size: 35px; }

.personPosition {
  font-size: 25px; }

.personPhoto {
  width: 100px;
  height: 100px;
  border-radius: 50px;
  line-height: 100px;
  background: white;
  overflow: hidden; }
  .personPhoto img {
    height: 100%;
    width: 100%; }

@media (min-width: 450px) {
  .cntPerson {
    flex-flow: row; }
  .personPhoto {
    margin-right: 30px; }
  .cntName {
    text-align: left; } }



