.contentRow {
  display: flex;
  flex-flow: row wrap;
  align-items: baseline; }

.rowIcon {
  width: 34px;
  text-align: center;
  margin-right: 15px; }

.circle {
  display: inline-block;
  color: white;
  font-size: 6px;
  background: #373d48;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  line-height: 10px; }
  .circle.middle {
    font-size: 16px;
    width: 34px;
    height: 34px;
    border-radius: 17px;
    line-height: 34px; }

.rowContent {
  flex: 1; }

.rowPeriod {
  flex: 1 100%;
  margin-left: 49px; }

@media (min-width: 900px) {
  .contentRow {
    flex-wrap: nowrap; }
  .rowPeriod {
    order: -1;
    margin-left: 0;
    flex: 0 auto;
    width: 65px; } }



