.sectionList {
  position: relative; }

.vertLine {
  width: 2px;
  background: #ddd;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 16px;
  z-index: -1; }

@media (min-width: 900px) {
  .vertLine {
    width: 2px;
    background: #ddd;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 81px; } }



